var SecurityExample = /** @class */ (function () {
    function SecurityExample(security) {
        this.security = security;
        this.perms = [];
    }
    /**
     * @return {?}
     */
    SecurityExample.prototype.shufflePermissions = /**
     * @return {?}
     */
    function () {
        /** @type {?} */
        var numOfPerms = Math.floor(Math.random() * 2) + 1;
        this.perms = this.shuffle(['A', 'B', 'C']).slice(0, numOfPerms);
        this.security.clear();
        this.security.grant(this.perms);
    };
    /**
     * @param {?} array
     * @return {?}
     */
    SecurityExample.prototype.shuffle = /**
     * @param {?} array
     * @return {?}
     */
    function (array) {
        /** @type {?} */
        var currentIndex = array.length;
        /** @type {?} */
        var temporaryValue;
        /** @type {?} */
        var randomIndex;
        // While there remain elements to shuffle...
        while (currentIndex !== 0) {
            // Pick a remaining element...
            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex -= 1;
            // And swap it with the current element.
            temporaryValue = array[currentIndex];
            array[currentIndex] = array[randomIndex];
            array[randomIndex] = temporaryValue;
        }
        return array;
    };
    SecurityExample.decorators = [
        { type: Component, args: [{
                    selector: 'security-example',
                    template: "<div>\r\n    <p *bhUnless=\"'A'\">Shown if has permission <strong>A</strong></p>\r\n    <p *bhUnless=\"'B'\">Shown if has permission <strong>B</strong></p>\r\n    <p *bhUnless=\"'C'\">Shown if has permission <strong>C</strong></p>\r\n    <p *bhUnless=\"'A && B'\">Shown if has permissions <strong>A && B</strong></p>\r\n    <p *bhUnless=\"'A && C'\">Shown if has permissions <strong>A && C</strong></p>\r\n    <p *bhUnless=\"'A && B && C'\">Shown if has permissions <strong>A && B && C</strong></p>\r\n    <p *bhUnless=\"'A || B'\">Shown if has permissions <strong>A || B</strong></p>\r\n    <p> Permissions are : <strong>{{ perms | json }}</strong></p>\r\n    <button (click)=\"shufflePermissions()\">Shuffle</button>\r\n</div>\r\n",
                    styles: [""]
                }] }
    ];
    /** @nocollapse */
    SecurityExample.ctorParameters = function () { return [
        { type: Security }
    ]; };
    return SecurityExample;
}());

